<template>
  <div class="home">
    <div class="container">
      <div class="card">
        <!-- <h3>Upload Files</h3> -->
        <div class="drop_box">
          <header>
            <h4>Select File here</h4>
          </header>
          <p>Files Supported: JSON Text only</p>
          <input
            type="file"
            hidden
            accept=".json"
            id="fileID"
            ref="fileInput"
            style="display: none"
            @change="handleFileUpload"
          />
          <button class="btn" @click="handleBtnClick">Choose File</button>
          <p>{{ fileName }}</p>
        </div>
      </div>
    </div>
    <br /><br />

    <div class="container">
      <div class="vertical-center">
        <a href="#iframecontainer" v-bind:class="{ disable: isDisabled }">
          <button
            type="button"
            class="btnViewer"
            v-bind:class="{ disable: isDisabled }"
            @click="sendDataToViewer"
          >
            View in 3D
          </button>
        </a>
      </div>
    </div>
    <div
      id="iframecontainer"
      v-bind:style="displayFrame ? 'display: block;' : 'display: none;'"
      class="iframe-container"
    >
      <iframe
        id="myIframe"
        :src="signallingServerAddress"
        width="1600"
        height="900"
      ></iframe>
    </div>
  </div>
</template>

<script>
export default {
  name: "Home",
  data: function () {
    return {
      textboxes: [],
      newTextbox: {
        text: "",
        x: 0,
        y: 0,
      },
      fileName: "",
      isDisabled: true,
      displayFrame: false,
      // Json message to send to the unreal app
      jsonMessage: {
        type: "TestType",
        message: [],
      },
      signallingServerAddress: "http://localhost:80",
      // signallingServerAddress:
      //   "https://connector.eagle3dstreaming.com/v5/EmG/CPlus/config?appVersion=3",
      iFrameScale: 0.75,
    };
  },
  methods: {
    handleBtnClick() {
      this.$refs.fileInput.click();
    },
    handleFileUpload(event) {
      const file = event.target.files[0];

      if (file.type === "application/json") {
        const reader = new FileReader();
        this.fileName = file.name;
        reader.onload = (e) => {
          const jsonData = JSON.parse(e.target.result);
          // Do something with the JSON data
          this.jsonMessage = jsonData;
          this.isDisabled = false;
          console.log(jsonData);
        };

        reader.readAsText(file);
      } else {
        // Show an error message or do something else
        alert("Please select a JSON file");
      }
    },
    sendDataToViewer: function () {
      console.log(JSON.stringify(this.jsonMessage));
      this.displayFrame = true;
      //this.scrollToDiv();
      // Send a json message via the iFrame
      document
        .getElementById("myIframe")
        .contentWindow.postMessage(JSON.stringify(this.jsonMessage), "*");
    },
    addCoordinate() {
      this.textboxes.push({
        x: this.newTextbox.x,
        y: this.newTextbox.y,
      });
      this.newTextbox.x = 0;
      this.newTextbox.y = 0;
      console.log(JSON.stringify(this.textboxes));
    },
    sendMessage: function () {
      console.log("Sending message from Vue...");
      this.jsonMessage.message = this.textboxes;
      console.log(JSON.stringify(this.jsonMessage));
      // Send a json message via the iFrame
      document
        .getElementById("myIframe")
        .contentWindow.postMessage(JSON.stringify(this.jsonMessage), "*");
    },
    addListener: function () {
      // Adds a listener for any messages coming from the iFrame
      window.onmessage = function (e) {
        // This is where you will handle all return types, other messages may be sent to the window so you need to handle them accordingly.
        // As we are only expecting strings, ignore any objects
        if (typeof e.data !== "object") {
          // E.g. ignore webpack messages
          if (!e.data.includes("webpack")) {
            alert("Message Recieved from Signalling Server: " + e.data);
          }
        }
      };
    },
    resizeIFrame: function () {
      document.getElementById("myIframe").width =
        window.innerWidth * this.iFrameScale;
      document.getElementById("myIframe").height =
        (window.innerWidth * this.iFrameScale) / 1.778;
    },
    addResizeListener: function () {
      window.addEventListener("resize", this.resizeIFrame);
    },

    scrollToDiv() {
      const targetElement = document.getElementById("iframecontainer");
      if (targetElement) {
        targetElement.scrollIntoView({ block: "center" });
      }
    },
  },
  mounted: function () {
    // Add the listener for any messages coming from the unreal app
    this.addListener();
    this.addResizeListener();
    this.resizeIFrame();
  },
};
</script>

